@use '../../../../styles/base.scss';


.single-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .content-container {
    img {
      background-color: base.$font-white;
      border-radius: 50%;
      box-shadow: base.$box-shadow-medium;
    }
  }

  .badge-delete-container {
    align-self: flex-start
  }

  .character {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: -30px;
    z-index: 1;

    span {
      background-color: base.$font-dark-red;
      color: base.$font-white;
      border-radius: 0.5em;
      padding: 0.4em 0.5em;
      font-weight: bold;
      font-size: 15px;
      box-shadow: base.$box-shadow-medium;
    }

    img {
      box-shadow: base.$box-shadow-medium;
    }
  }
}

.single-box-dual {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .content-container {
    img {
      background-color: base.$font-white;
      border-radius: 50%;
      box-shadow: 0px 6px 12px rgb(3 3 3);
    }
  }

  .box-delete-container {
    align-self: flex-start
  }

  .character-1 {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: -20px;
    z-index: 1;

    span {
      background-color: base.$font-dark-red;
      color: base.$font-white;
      border-radius: 0.5em;
      padding: 0.3em 0.5em;
      font-weight: bold;
      font-size: 14px;
      box-shadow: base.$box-shadow-medium;
    }

    img {
      box-shadow: base.$box-shadow-medium;
    }
  }

  .character-2 {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: -47px;
    z-index: 1;

    span {
      background-color: base.$font-dark-red;
      color: base.$font-white;
      border-radius: 0.5em;
      padding: 0.3em 0.5em;
      font-weight: bold;
      font-size: 14px;
      box-shadow: base.$box-shadow-medium;
    }

    img {
      box-shadow: base.$box-shadow-medium;
    }
  }
}

@media screen and (max-width: 1546px) {
  .single-box {
    span {
      padding: 0.3em 0.4em;
      font-size: 13px;
    }
  
    .character {
      span {
        padding: 0.3em 0.4em;
        font-size: 13px;
      }
    }
  }
  
  .single-box-dual {
    .character-1 {

      span {
        padding: 0.2em 0.4em;
        font-size: 13px;
      }
    }
  
    .character-2 {
      span {
        padding: 0.2em 0.4em;
        font-size: 13px;
      }
    }
  }
}