@use '../../styles/base.scss';

.team {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  font-family: base.$site-body-font;
  border-radius: 1em;
  background-color: #716f9a69;
  box-shadow: base.$box-shadow-dark;
  padding: 2em 0;

  .team-close-button {
    align-self: flex-end;

    button {
      background-color: transparent;
      border-style: none;
      cursor: pointer;
      color: base.$font-white;
      font-size: x-large;
      margin-right: 1em;
    }
  
    button:active {
      transform: scale(0.95);
    }
  }

  .team-carousel {
    margin-bottom: 3em;

    .arrow {
      font-size: 50px;
      color: rgba(245, 245, 245, 0.259);
      font-weight: bolder;
    }
  
    .team-carousel-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 475px;
      height: 200px;
      background-color: transparent;
      border-radius: 1em;
      position: relative;
    
      .meet-team {
        color: base.$font-white;
        font-size: 55px;
        font-weight: bold;
        margin-left: 0.25em;
      }
    
      .name {
        color: base.$font-white;
        font-size: 15px;
        position: absolute;
        bottom: 10px;
        left: 85px;
        z-index: 1;
        background-color: #7b799e;
        border-radius: 50%;
        padding: 2em;
        box-shadow: 0px 2px 6px rgb(154 151 151);

        .first-name {
          font-size: 35px;
          font-weight: bold;
        }
      }
    
      img {
        height: 175px;
        width: 175px;
        background-color: base.$font-white;
        border-radius: 2em;
        margin-bottom: 1.5em;
        margin-left: 5em;
        object-fit: cover;
        box-shadow: 0px 2px 6px rgb(154 151 151);
        z-index: 0;
      }
    }    
  }
}