footer {
  padding-top: 7em;
  background-color: whitesmoke;

  .letters {
    display: flex;
    justify-content: center;
    position: relative;
    font-size: 17vw;
    font-family: 'Itim', cursive;
  }

  // Specifies animation steps and styles for different animation points on Y axis
  .letters:hover {
    @keyframes bounce {
      0% {
        transform: translateY(0);
      }
      20% {
        transform: translateY(-50px);
      }
      50% {
        transform: translateY(0);
      }
      70% {
        transform: translateY(-10px);
      }
      100% {
        transform: translateY(0);
      }
    }
  
    // Name, duration, timing, iteration count, delay
    .c { animation: bounce 2.5s ease infinite;
    }
    .r {
      animation: bounce 2.5s ease infinite .1s;
    }
    .i {
      animation: bounce 2.5s ease infinite .2s;
    }
    .t {
      animation: bounce 2.5s ease infinite .3s;
    }
    .i {
      animation: bounce 2.5s ease infinite .4s;
    }
    .q {
      animation: bounce 2.5s ease infinite .5s;
    }
    .u {
      animation: bounce 2.5s ease infinite .6s;
    }
    .e {
      animation: bounce 2.5s ease infinite .7s;
    }
    .pencil-icon {
      animation: bounce 2.5s infinite .8s;
    }
  }
}


